import {getBlockDefinitions} from "./blockDefinitions.js";
//console.log( 'getBlockDefinitions', getBlockDefinitions );

let blockDefinitions, blockDefinitionsByTagName;

function setBlockDefinitions(){
    // need to load them only once
    if( blockDefinitions == null ){        
        blockDefinitions = getBlockDefinitions();
        blockDefinitionsByTagName = {};
        blockDefinitions.forEach((block) => {
            blockDefinitionsByTagName[block.tagName] = block;
        });
    }
}

export default class HtmlTemplateParser {
  constructor() {
    throw new Error(
      "HtmlTemplateParser is a static class and cannot be instantiated"
    );
  }

  static replaceSpecialElements(htmlString){
    return htmlString.replace(/<%/g, '<JULIA_CODE>').replace(/%>/g, '</JULIA_CODE>');
  }
  static restoreSpecialElements(htmlString){
    return htmlString.replace(/<JULIA_CODE>/gi, '<% ').replace(/<\/JULIA_CODE>/gi, ' %>');
  }

  static encodeAttributesInDOM(element) {

    // Ensure the element is a Node
    if (!(element instanceof Node)) {
    throw new Error("Element is not a Node");
    }

    // Call the callback for the current element
    HtmlTemplateParser.replaceElementAttributes(element);

    // Recursively traverse the child nodes
    for (let i = 0; i < element.childNodes.length; i++) {
    HtmlTemplateParser.encodeAttributesInDOM(
        element.childNodes[i]
    );
    }
}

static replaceElementAttributes( element ){
    setBlockDefinitions();
    let modelVariables = window.sharedData.modelFieldsByName;
    let gbPrefix = 'gb_';
    let bcTypeC = gbPrefix + 'c__';
    let bcTypeB = gbPrefix + 'b__';
    let specialDynamicAttributeNames = [ 'v-model', 'v-bind', 'v-on:click' ];
    let excludedAttributeNames = [ 'class', 'style', 'id'];
    let componentName = element.nodeName;
    let componentDefinition = blockDefinitionsByTagName[componentName];
            
    // Do not parse elements without component definition
            if( !componentDefinition ){
        return
    }
    let componentPropertiesDefinitions = componentDefinition.properties;
    let elementAttributeNames = element.getAttributeNames();
    elementAttributeNames.forEach( (attributeName) => {
        // Do not parse excluded attributes such as id, class, style...
        if( excludedAttributeNames.indexOf(attributeName) > -1 ){
            return;
        }

        let hasColon = attributeName.startsWith(':');
        let cleanName = hasColon ? attributeName.slice(1) : attributeName;
        let isDynamic = hasColon || specialDynamicAttributeNames.some( (specialName) => {
            return attributeName.indexOf(specialName) === 0;
        });
        let attributeValue = element.getAttribute( attributeName );
        let attributeDefinition = componentPropertiesDefinitions.find( el => el.name === cleanName );
        let attributeValueMapsToModelVariable = modelVariables[attributeValue] != null;

        // Only consider attributes that are defined in the component definition
        if( attributeDefinition ){
            // if its dynamic, it's either a binding or a constant
            if( isDynamic ){
                if( attributeValueMapsToModelVariable ){
                    element.setAttribute( bcTypeB+ cleanName, attributeValue );
                } else {
                    element.setAttribute( bcTypeC+ cleanName, attributeValue );
                }
            // If it's not dynamic', it's a constant
            }else{
                if( attributeValue=='' ){
                    element.setAttribute( bcTypeC+ cleanName, "true" );
                }else {
                    element.setAttribute( bcTypeC+ cleanName, attributeValue );
                }
            }
            element.removeAttribute( attributeName );
        }
    });
}

static revertElementAttributes(element) {
    setBlockDefinitions();
    let gbPrefix = 'gb_';
    let bcTypeC = gbPrefix + 'c__';
    let bcTypeB = gbPrefix + 'b__';
    let componentName = element.nodeName;
    let componentDefinition = blockDefinitionsByTagName[componentName];
    if( !componentDefinition ){
        return;
    }
    let componentPropertiesDefinitions = componentDefinition.properties;
    
    let elementAttributeNames = element.getAttributeNames();
    elementAttributeNames.forEach((attributeName) => {
        let attributeValue = element.getAttribute( attributeName );
        if (attributeName.startsWith(bcTypeC)) {
            let originalName = attributeName.replace(bcTypeC, '');
            let attributeDefinition = componentPropertiesDefinitions.find( el => el.name === originalName );
            if( !attributeDefinition ){
                throw new Error( 'Attribute definition not found for: ' + originalName );
            }
            let dataType = attributeDefinition.type;
            //let dataType = HtmlTemplateParser.guessDataType(attributeValue);
            if( componentName == "PLOTLY" && ["data", "layout", "config"].includes(originalName) ){
                // To-do: revise if we need to handle this further
            }else if( dataType != 'String' && !originalName .startsWith('v-on:') ){
                originalName =  ':' + originalName;
            }

            element.setAttribute(originalName, attributeValue );
            element.removeAttribute(attributeName);
        } else if (attributeName.startsWith(bcTypeB)) {
            let originalName = attributeName.replace(bcTypeB, '');
            let bindingPrefix = (originalName === 'v-model' || originalName.startsWith('v-on:') ) ? '' : ':';
            element.setAttribute(bindingPrefix + originalName, attributeValue );
            element.removeAttribute(attributeName);
        }
    });
}

static decodeAttributesInDOM(rootElement) {
    let elements = rootElement.querySelectorAll('*');
    elements.forEach(element => {
        HtmlTemplateParser.revertElementAttributes(element);
    });
}

static guessDataType(input) {
    if (typeof input === 'number' || /^-?\d*\.?\d+$/.test(input)) {
        return 'Number';
    } else if (input === 'true' || input === 'false' || typeof input === 'boolean') {
        return 'Boolean';
    } else if (Array.isArray(input) || /^\[.*\]$/.test(input)) {
        return 'Array';
    } else {
        return 'String';
    }
}

static removeEmptyTextNodes(htmlString) {
    // Parse the HTML string into a DOM document
    const parser = new DOMParser();
    const doc = parser.parseFromString(htmlString, 'text/html');
  
    // Function to check if a node is an empty text node
    const isEmptyTextNode = node => node.nodeType === Node.TEXT_NODE && !/\S/.test(node.nodeValue);
  
    // Recursive function to remove all empty text nodes from a given node
    const removeEmptyNodes = node => {
      const childNodes = Array.from(node.childNodes);
      childNodes.forEach(child => {
        if (isEmptyTextNode(child)) {
          // Remove the empty text node
          node.removeChild(child);
        } else {
          // Recursively check this node's children
          removeEmptyNodes(child);
        }
      });
    };
  
    // Start the removal process from the body of the parsed document
    removeEmptyNodes(doc.body);
  
    // Serialize the body back into a string
    const serializer = new XMLSerializer();
    const newHtmlString = serializer.serializeToString(doc.body);
    // The serialized string includes <body> tags, so we remove them to only return the inner HTML
    return newHtmlString.replace(/^<body>|<\/body>$/g, '');
  }
  
}
