<template>
    <div style="position:relative;width: 280px;">
        <div class="flex flex-col items-center justify-center p-6" style="padding: 0px;">
            <div class="mb-4 w-full max-w-xl">
                <textarea rows="2" v-model="question" placeholder="Ask AI for help on how to use Genie"
                    class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" />
            </div>

            <div v-if="error" class="">
                <p class="text-red-600" style="margin-top: -15px;">{{ error }}</p>
            </div>

            <vscode-button @click="askQuestion" style="width: 100px !important;">Ask AI</vscode-button>
        </div>
    </div>
</template>

<script setup>

import axios from 'axios';
import _ from 'underscore';
import {computed, ref, onMounted} from 'vue';
import Configuration from '../../utils/Configuration';


const props = defineProps(['appData']);


const question = ref('');
const error = ref(null);



const askQuestion = async () => {
    if (question.value.trim() === '') {
        error.value = 'Please enter a question.';
        return;
    }
    window.parent.postMessage( {command:"openAiDocs", query:question.value }, "*" );
    question.value = "";
    error.value = null;
};



</script>

<style lang="scss" scoped>
@import "../../styles/globalStyles.scss";


</style>
